import React, { useState } from 'react';
import {Navbar, Nav, Button, Container, Modal, Form} from "react-bootstrap";
import {Link} from 'react-router-dom';
import styled from "styled-components";
import Breadcrumbs from "./Breadcrumbs";


const Styles = styled.div`
  a, .navbar-brand, .navbar-nav, .nav-link {
    color: #2BA7CE;
    text-decoration: none;

    &:hover {
      color: #f50000;
    }
  }
`

export default function Navibar() {

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            <Styles>
                <Navbar collapseOnSelect expand={"lg"} bg={"dark"} variant={"dark"}>
                    <Container>
                        <Navbar.Brand><Link to={"/"}></Link></Navbar.Brand>
                        <Navbar.Toggle aria-controls={"responsive-navbar-nav"}/>
                        <Navbar.Collapse id={"responsive-navbar-nav"}>
                            <Nav className={"mr-auto"}>
                                <Nav.Link><Link to={"/"}></Link></Nav.Link>
                                <Nav.Link><Link to={"/users"}></Link></Nav.Link>
                                <Nav.Link><Link to={"/about"}> </Link></Nav.Link>
                            </Nav>
                            <Nav>
                                <Button variant={"info"} className={"mr-3"} onClick={handleShow}></Button>
                                <Button variant={"warning"} onClick={handleShow}></Button>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>

                <Container>
                    <Breadcrumbs />
                </Container>

            </Styles>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group controlId={"formBasicEmail"}>
                            <Form.Label> email</Form.Label>
                            <Form.Control type={"email"} placeholder={"  email"} />
                            <Form.Text className={"text-muted"}>     .</Form.Text>
                        </Form.Group>
                        <Form.Group controlId={"formBasicPassword"}>
                            <Form.Label> </Form.Label>
                            <Form.Control type={"password"} placeholder={"  "} />
                        </Form.Group>
                        <Form.Group controlId={"formBasicCheckbox"}>
                            <Form.Check type={"checkbox"} label={" "} />
                        </Form.Group>
                    </Form>
                </Modal.Body>
            </Modal>
        </>
    );
}
